import './app-product-details.scss';
import { IProduct, IProductDetails } from '../../../_models/app-product-model';
import { useMailto } from '../../../_service/app-mail.service';
import { useEffect, useState } from 'react';
import ShipmentService from '../../../_service/app-shipment.service';
import { logger } from '../../../_core/logger.service';
import { Box, Skeleton } from '@mui/material';
import { Product_Keys_To_Extract } from '../../../_core/constant';
import ImageCard from '../app-image-card/image-card';
import { ProductKeys } from '../../../enum/order.enum';
import { useSelector } from 'react-redux';
import { RootReducer } from '../../../_models/root-reducer-model';

const AppProductDetails = () => {
    const handleMailClick = useMailto();
    const [orderDetails, setOrderDetails] = useState<IProduct[] | null>(null);
    const productDetail: IProduct[] = useSelector((state: RootReducer) => state.order.productDetails);

    useEffect(() => {
        getOrderDetails(productDetail);
    }, [productDetail]);

    const getOrderDetails = (productDetails: IProduct[]) => {
        // ShipmentService.getOrderDetailsById(7678).then((res) => {
        const newArray: any = productDetails.map((ele) => ({
            ...ele,
            productDetails: formDataFromResponse(ele)
        }));
        setOrderDetails(newArray);
        // }).catch((error) => {
        //     logger.error(error);
        //     setOrderDetails(null);
        // })
    }

    function formDataFromResponse(product: IProduct): IProductDetails[] {
        const newArray = Product_Keys_To_Extract.map(key => {
            const newObj: IProductDetails = {
                name: key,
                value: product[key]
            }
            return newObj;
        });
        return newArray;
    }

    function sendMail(email: string | null | undefined) {
        if (email) {
            handleMailClick(email);
        }
    }

    return (
        <>
            {!orderDetails
                ?
                <div>
                    <Box>
                        <Skeleton animation='wave' style={{ height: 50 }} />
                        <Skeleton animation='wave' style={{ height: 248 }} />
                        <Skeleton animation='wave' style={{ height: 50 }} />
                        <Skeleton animation='wave' style={{ height: 248 }} />
                    </Box>
                </div>
                :
                orderDetails.map((ele: IProduct, index: number) => {
                    return <div key={ele.itemId}>
                        <div className="product_detail_header_container">
                            <label className="product_detail_secondary_header">Product {index + 1}</label>
                            {
                                ele.email &&
                                <button className='send_mail_button' onClick={() => sendMail(ele.email)}>Send Mail</button>
                            }
                        </div>
                        <div className='prodct_detail_body'>
                            <div className="product_grid_container">
                                {ele.productDetails
                                    &&
                                    ele.productDetails.map((item: IProductDetails, index: number) => {
                                        return <div key={index} className="product_grid_item">
                                            <label className='product_details_name'>{ProductKeys[item.name as keyof typeof ProductKeys] || item.name}:</label><br />
                                            <div className='p-t-5 '>
                                                <label className='prduct_detail_value'>{item.value ?? '--'}</label>
                                            </div>
                                        </div>
                                    })
                                }
                            </div>
                            {
                                ele.sellerAckData &&
                                <div className='seller_note' key={index}>
                                    <label className='product_details_name'>Seller's Note:</label>
                                    <p className='note_description'>{ele.sellerAckData.notes}</p>
                                    <div>
                                        <ImageCard images={ele.sellerAckData.attachment} />
                                    </div>
                                </div>
                            }
                        </div>
                    </div >
                })
            }
        </>
    );
}

export default AppProductDetails;
