import http from "../_core/helpers/axios/axios-instance";
import { Params } from "../_models/app-common-model";
import { IProduct } from "../_models/app-product-model";
import { IOrder } from "../_models/order";
import { ReportedList } from "../_models/app-common-model";
import { TrackingInfo } from "../_models/shipment-model";
import { InitiateRefundParams } from "../_types/app-shipment-types";

const GET_ORDER = '/support/order/';
const TRACK_ORDER = '/shipengine/track';
const GET_REPORTED_LIST = '/support/reports';
const INITIATE_REFUND = '/orders/transfer_fund';

const getOrderDetailsById = (orderId: number) => {
    return http.get<any, IProduct[]>(`${GET_ORDER}${orderId}`);
};

const trackPackage = (itemId: number) => {
    return http.get<TrackingInfo>(TRACK_ORDER, {
        params: { itemId }
    });
};

const getOrderReportList = (status: number, type: number, searchTerm?: string) => {
    const params: Params = {
        status,
        type
    }

    if (searchTerm) params.searchTerm = searchTerm;
    return http.post<ReportedList[]>(GET_REPORTED_LIST, params)
}

const initiateRefund = (params: InitiateRefundParams) => {
    return http.put<InitiateRefundParams, any>(INITIATE_REFUND, params);
}

const ShipmentService = {
    getOrderDetailsById,
    trackPackage,
    getOrderReportList,
    initiateRefund
}

export default ShipmentService;
