import './search-bar.scss';
import lensIcon from '../../../../assets/icons/Shape.svg'
import { useEffect, useState } from 'react';
import CloseIcon from '@mui/icons-material/Close';

interface SearchProps {
    changeHandler: (value: string) => void;
    searchText?: string;
}
const SearchBar: React.FC<SearchProps> = ({ changeHandler, searchText }) => {
    const [inputValue, setInputValue] = useState('');

    useEffect(() => {
        setInputValue(searchText ?? '');
    }, [searchText])

    const onChangeHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
        setInputValue(event.target.value);
        setTimeout(() => {
            changeHandler(event.target.value);
        }, 2000);
    }

    function iconClick(e: React.MouseEvent) {
        setInputValue('');
        changeHandler('');
    }

    return (
        <>
            <div className='search'>
                <input placeholder='Search' className='search-container' value={inputValue} onChange={onChangeHandler}></input>
                <img className='lensIcon' src={lensIcon} />
                <CloseIcon className='closeIcon' onClick={iconClick} />
            </div>
        </>
    );
}

export default SearchBar;