import { TrackingInfo } from '../../../_models/shipment-model';
import { Step, StepConnector, StepLabel, Stepper, Typography } from '@mui/material';
import CircleIcon from '@mui/icons-material/Circle';
import './track-order.scss';
import { ITrackModel } from '../app-shipment-overview/app-shipment-overview';

interface TrackingProps {
    trackingData: ITrackModel;
}

const TrackOrder = (props: TrackingProps) => {
    function formatDateTime(dateTimeString: string): string {
        const date = new Date(dateTimeString);
        const options: Intl.DateTimeFormatOptions = { year: 'numeric', month: 'short', day: 'numeric', hour: 'numeric', minute: '2-digit', hour12: true };
        return date.toLocaleString('en-US', options);
    }

    return (
        <div className='stepper_container'>
            <Stepper classes={{ root: 'stepper_root' }} activeStep={props?.trackingData?.fill} orientation="vertical"
                connector={<StepConnector classes={{
                    lineVertical: "lineVertical",
                    completed: "completed_line",
                    root: "root_connector"
                }} />}>
                {
                    props?.trackingData?.trackInfo?.map((info: TrackingInfo, index: number) =>
                        <Step key={index} classes={{ root: "step_root" }}>
                            <StepLabel
                                icon={<>
                                    <div className="stepperIcon">
                                        <CircleIcon classes={{ root: "icon_root" }} />
                                        <div className='outer_circle'></div>
                                    </div>
                                </>}
                                classes={{
                                    vertical: "vertical_label",
                                    labelContainer: "label_container",
                                    iconContainer: "stepper_icon"
                                }}
                            >{
                                    <>
                                        <Typography className='regular-18 font-color'>{info.status_description}</Typography>
                                        <Typography className='regular-18 subText_color'>{info.carrier_status_description}</Typography>
                                        <Typography className='regular-18 subText_color'>
                                            {info.city_locality ? `${info.city_locality}, ` : ''}
                                            {info.state_province ? `${info.state_province}, ` : ''}
                                            {info.country_code ? `${info.country_code}` : ''}
                                            {info.postal_code ? ` - ${info.postal_code}` : '.'}</Typography>
                                        <Typography className='regular-18 subText_color date_view'>{formatDateTime(info.occurred_at)}</Typography>
                                    </>
                                }
                            </StepLabel>
                        </Step>
                    )
                }
            </Stepper>
        </div>
    )
}

export default TrackOrder;
