import { Avatar } from '@mui/material';
import './app-order-report-view.scss';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import { useSelector } from 'react-redux';
import { RootReducer } from '../../../_models/root-reducer-model';
import PopoverComponent from '../app-popover/popover-component';
import { Options } from '../../../_models/app-common-model';
import { useState } from 'react';
import AppModelService from '../../../_service/app-model-service';
import ProfileDetails from '../app-profile-details/profile-details';
import AppOrderDetail from '../app-order-detail/app-order-detail';
import AppShipmentTrack from '../app-shipment-track/app-shipment-track';
import AppInitiateRefund from '../app-initiate-refund/app-initiate-refund';
import Button from '../button/button';
import { ReportedList } from '../../../_models/app-common-model';
import ImageCard from '../app-image-card/image-card';
import { IProduct } from '../../../_models/app-product-model';

interface IOrderDetail {
    trackShipmentClick: (flag: boolean, res: IProduct) => void;
}

const AppOrderReportView = (props: IOrderDetail) => {
    const [popoverOpen, setPopoverOpen] = useState(false);
    const [pAnchorEl, setpAnchorEl] = useState<EventTarget | any>(null);
    const configData = [{ value: 1, displayName: 'View profile' }, { value: 2, displayName: 'Show Order Details' }];
    const order: ReportedList = useSelector((state: RootReducer) => state.order.data);

    const openAction = (event: React.MouseEvent<HTMLElement>) => {
        setPopoverOpen(true);
        setpAnchorEl(event.currentTarget);
    }

    const closePopover = () => {
        setPopoverOpen(false);
    }

    const selectedData = (ev: Options) => {
        switch (ev.value) {
            case 1:
                AppModelService.createPopUp({
                    compoent: <ProfileDetails id={order.userId} />,
                    headerName: '',
                    header: false,
                    footer: false,
                    width: 580,
                    height: 560,
                    class: 'main_content',
                    showCloseIcon: true
                });
                break;
            case 2:
                AppModelService.createPopUp({
                    compoent: <AppOrderDetail />,
                    headerName: '',
                    header: false,
                    footer: false,
                    width: 820,
                    height: 760,
                    class: 'main_content',
                    showCloseIcon: true
                });
        }
    }

    const trackShipment = () => {
        AppModelService.createPopUp({
            compoent: <AppShipmentTrack trackClick={(res) => props.trackShipmentClick(true, res)} />,
            headerName: '',
            header: false,
            footer: false,
            width: 738,
            height: 350,
            class: 'main_content',
            showCloseIcon: true
        });
    };

    const initiateRefund = () => {
        AppModelService.createPopUp({
            compoent: <AppInitiateRefund />,
            headerName: '',
            header: false,
            footer: false,
            width: 738,
            height: 418,
            class: 'main_content',
            showCloseIcon: true
        });
    };

    const initiateInsurance = () => {
        // Not implemented
    }

    return (
        <>
            <div className='orderView'>
                <div className='order_content'>
                    <div className='profile'>
                        <div className='detail'>
                            <Avatar className='profileIcon' src={order?.profile_pic ?? ''} alt={order?.name}></Avatar>
                            <p className='name regular-18'>{order?.display_name ?? order?.name}</p>
                        </div>
                        <div onClick={(e) => openAction(e)}>
                            <MoreVertIcon className='three_dots' />
                        </div>
                    </div>
                    <div className='order_cell'>
                        <div className='order_detail_container'>
                            <p className='option regular-18'>
                                <span className='primary_text_color'>Query: </span>
                                <span className='quinary_text_color'>{order?.question}</span>
                            </p>
                            <div>
                                <label>
                                    <span className='primary_text_color'>Description: </span>
                                    <span className='quinary_text_color'>{order?.detail}</span>
                                </label>
                            </div>
                            {order?.attachments &&
                                <div className='order_attachment_container'>
                                    <label>Attachments:</label>
                                    <ImageCard images={order?.attachments} />
                                </div>
                            }
                        </div>
                    </div>
                </div>
                <div className='order_action_container'>
                    <div className="order_action_grid">
                        <div className="button-wrapper"><Button click={trackShipment}>Track Shipment</Button></div>
                        <div className="button-wrapper"><Button click={initiateRefund}>Initiate Payment</Button></div>
                        <div className="button-wrapper"><Button click={initiateInsurance}>Initiate Insurance</Button></div>
                    </div>
                </div>
            </div>
            <PopoverComponent open={popoverOpen} anchorEl={pAnchorEl} closePopover={closePopover} configData={configData} selectedData={selectedData} />
        </>
    );
}

export default AppOrderReportView;
