import { Typography } from "@mui/material";
import Dropdown from "../app-dropdown/app-dropdown";
import { useEffect, useState } from "react";
import './app-initiate-refund.scss';
import { Options } from "../../../_models/app-common-model";
import { IProduct } from "../../../_models/app-product-model";
import { RootReducer } from "../../../_models/root-reducer-model";
import { useSelector } from "react-redux";
import ShipmentService from "../../../_service/app-shipment.service";
import { logger } from "../../../_core/logger.service";
import { InitiateRefundParams } from "../../../_types/app-shipment-types";
import AppToasterService from "../../../_service/app-alert-service";
import AppModelService from "../../../_service/app-model-service";

const AppInitiateRefund = () => {
    const productDetail: IProduct[] = useSelector((state: RootReducer) => state.order.productDetails);
    const [options, setOptions] = useState<Options[] | any>([]);
    var selectedItem: number | number[] | null;
    var seletcedUserType: Options | null;
    const userType: Options[] = [
        {
            value: 1,
            displayName: 'Seller'
        },
        {
            value: 2,
            displayName: 'Buyer'
        }
    ]

    useEffect(() => {
        const dropdownValues: Options[] = productDetail.map(e => { return { value: e.fulfilmentId ?? 0, displayName: e.itemName ?? '' } });
        if (Array.isArray(dropdownValues)) setOptions(dropdownValues);
    }, [productDetail]);

    const handleSelect = (value: number | number[] | null) => {
        if (Array.isArray(value)) {
            if (productDetail) selectedItem = productDetail.filter(e => value.includes(e.fulfilmentId)).map((f) => f.itemId);
        } else if (value) {
            if (productDetail) selectedItem = productDetail.find(e => e.fulfilmentId == value)?.itemId ?? null;
        }
    };

    const handleUserSelection = (value: number | number[] | null) => {
        seletcedUserType = userType.find((el) => el.value == value) ?? null;
    }

    const initiatePayment = () => {
        var params: InitiateRefundParams = {
            "itemId": selectedItem
        }
        if (Array.isArray(selectedItem)) {
            if (selectedItem.length == 0 || !seletcedUserType?.value) {
                AppToasterService.showToaster({
                    message: 'To Initiate refund you need to select product and user type',
                    severity: 'warning'
                });
                return
            }
            params["itemId"] = selectedItem
        } else {
            if (!selectedItem || !seletcedUserType?.value) {
                AppToasterService.showToaster({
                    message: 'To Initiate refund you need to select product and user type',
                    severity: 'warning'
                });
                return
            }
        }

        if (seletcedUserType?.value == 1) params['toSeller'] = true
        else if (seletcedUserType?.value == 2) params['toBuyer'] = true

        ShipmentService.initiateRefund(params)
            .then((res) => {
                AppModelService.dismiss();
                AppToasterService.showToaster({
                    message: res.message ?? '',
                    severity: 'success'
                });
            }).catch((error) => {
                logger.error(error);
            });
    }
    return (
        <>
            <div className="header">
                <Typography className="semiBold-22 header-style">Initiate Refund</Typography>
            </div>
            <p className='input-label semiBold-18 font-color'>Select the product for refund: </p>
            <div className='input-container'>
                <Dropdown
                    options={options}
                    onSelect={handleSelect}
                    multiple={false}
                ></Dropdown>
            </div>
            <p className='input-label semiBold-18 font-color'>Select user: </p>
            <div className='input-container'>
                <Dropdown options={userType} onSelect={(res) => handleUserSelection(res)}></Dropdown>
            </div>
            {/* <p>
                <span className="input-text regular-16 font-color">The refund amount for this product is </span>
                <span className="price_label semiBold-18 ">$ 10.00</span>
            </p> */}
            <div className="button-container-initiate">
                <button className='initiate_button' onClick={initiatePayment}>Initiate</button>

            </div>
        </>
    );
}

export default AppInitiateRefund;
