import { Typography } from "@mui/material";
import './app-shipment-track.scss';
import Dropdown from '../../components/app-dropdown/app-dropdown'
import { useEffect, useState } from "react";
import Button from "../button/button";
import AppModelService from "../../../_service/app-model-service";
import { useSelector } from "react-redux";
import { RootReducer } from "../../../_models/root-reducer-model";
import { IProduct } from "../../../_models/app-product-model";
import AppToasterService from "../../../_service/app-alert-service";
import { Options } from "../../../_models/app-common-model";

type IShipmentTrackProps = {
    trackClick: (res: IProduct) => void;
}

const AppShipmentTrack = (props: IShipmentTrackProps) => {
    const productDetail: IProduct[] = useSelector((state: RootReducer) => state.order.productDetails);
    const [options, setOptions] = useState<Options[]>([]);
    const [selectedItem, setSelectedItem] = useState<IProduct | undefined | null>(null);

    useEffect(() => {
        const dropdownValues: Options[] = productDetail.flatMap(e => { return { value: e.fulfilmentId ?? 0, displayName: e.itemName ?? '' } });
        if (Array.isArray(dropdownValues)) setOptions(dropdownValues);
    }, [productDetail]);

    const handleSelect = (value: number | number[] | null) => {
        if (productDetail) setSelectedItem(productDetail.find(e => e.fulfilmentId == value));
    };

    const sendTrackItem = () => {
        if (!selectedItem) {
            AppToasterService.showToaster({
                message: 'Select product for tracking',
                severity: 'warning'
            });
            return;
        }
        AppModelService.dismiss();
        if (selectedItem) props.trackClick(selectedItem);
    }

    return (
        <>
            <div className="header">
                <Typography className="semiBold-22 header-style">Track Shipment</Typography>
            </div>
            <p className='input-label semiBold-18 font-color'>Select the product for tracking: </p>
            <div className='input-container'>
                <Dropdown options={options} onSelect={(value) => handleSelect(value)}></Dropdown>
            </div>
            {/* <p className='input-label semiBold-18 font-color'>Track ID: </p>
            <div className='input-container'>
                <textarea placeholder='Enter the tracking id details...' className='inputBox' name="input"></textarea>
            </div> */}
            <div className="button-container">
                <Button click={sendTrackItem}>Track</Button>
            </div >
        </>
    );
}

export default AppShipmentTrack;
