import { Box, Grid, Typography } from "@mui/material";
import './app-shipment-overview.scss';
import TrackOrder from "../track-order/track-order";
import { useEffect, useState } from "react";
import { TrackingInfo } from "../../../_models/shipment-model";
import ShipmentService from "../../../_service/app-shipment.service";
import { Logger } from "../../../_core/logger.service";
import AppShippingDetails from "../app-shipping-details/app-shipping-details";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { useSelector } from "react-redux";
import { RootReducer } from "../../../_models/root-reducer-model";
import { IProduct } from "../../../_models/app-product-model";

const logger = new Logger();

type PropsForShipmentOverview = {
    fulfilment: number;
    navigateToBack: (flag: boolean) => void;
    producDetails: IProduct | null;
}

export interface ITrackModel {
    trackInfo: TrackingInfo[] | [];
    fill: number;
}

const AppShipmentOverView = (props: PropsForShipmentOverview) => {
    const [trackInfo, setTrackInfo] = useState<ITrackModel | any>({});
    const productDetail: IProduct = useSelector((state: RootReducer) => state.order.productDetails);

    useEffect(() => {
        trackOrder(props.fulfilment)
    }, []);

    function trackOrder(itemId: number) {
        ShipmentService.trackPackage(itemId)
            .then((res: any) => {
                setTrackInfo(res);
            })
            .catch((err) => {
                logger.error(err?.message)
            })
    }

    const goBack = () => {
        props.navigateToBack(false)
    }

    return (
        <>
            <Grid container columnSpacing={2} height={1}>
                <Grid item md={7} lg={8} xl={8} className="shipment_container">
                    <Box sx={{ display: "flex" }} gap={'20px'}>
                        <div className="back_navigation" onClick={goBack}>
                            <ArrowBackIcon style={{ marginBottom: "5px" }} />
                            <Typography style={{ fontStyle: "italic" }} className="page-header">Back</Typography>
                        </div>
                        <Typography className="page-header">Shipment Overview</Typography>
                    </Box>
                    <Grid container className="shipment_overview_content">
                        <AppShippingDetails product={props.producDetails} />
                    </Grid>
                </Grid>
                <Grid item md={5} lg={4} xl={4} className="tracking_overview">
                    <Box>
                        <Typography style={{ fontStyle: "italic" }} className="page-header">Routing Information</Typography>
                    </Box>
                    <Grid container className="tracking_main">
                        <TrackOrder trackingData={trackInfo} />
                    </Grid>
                    <div className="fade_effect"></div>
                </Grid>
            </Grid>
        </>
    );
}

export default AppShipmentOverView;
