import React, { ChangeEvent, useEffect, useState } from 'react';
import './app-dropdown.scss';
import { Box, Select, SelectChangeEvent } from '@mui/material';
import MenuItem from '@mui/material/MenuItem';
import { ExpandMore } from '@mui/icons-material';
import { Options } from '../../../_models/app-common-model';


interface DropdownProps {
  options: Options[];
  multiple?: boolean;
  onSelect: (value: number | number[] | null) => void;
}

const Dropdown: React.FC<DropdownProps> = (props: DropdownProps) => {
  const [selectedOption, setSelectedOption] = useState<any>([]);

  const handleChange = (event: SelectChangeEvent) => {
    const {
      target: { value },
    } = event;

    if (value) setSelectedOption(value);
    if (Array.isArray(value)) {
      props.onSelect(
        value
        ?? null
      );
    } else {
      props.onSelect(
        parseInt(value, 10)
        ?? null
      );
    }
  };

  const displayName = (selected: number[] | number | string): string => {
    if (Array.isArray(selected) && selected.length == 0) {
      return 'Select';
    } else if (Array.isArray(selected)) {
      return props.options
        .filter((el: Options) => { return selected.includes(el.value as number) })
        .map((item) => item.displayName)
        .join(', ');
    } else if (typeof selected == 'number') {
      return typeof selected == 'number'
        ? props.options.find((el: Options) => el.value === selected)?.displayName ?? ''
        : ''
        ;
    }

    return '';
  }

  return (
    <>
      <Box sx={{
        flexGrow: 1,
        backgroundColor: "var(--secondary-color)",
        borderRadius: 4
      }}>
        <Select
          multiple={props.multiple}
          className='select'
          classes={{ root: 'roots', outlined: 'outline' }}
          value={selectedOption}
          onChange={handleChange}
          IconComponent={ExpandMore}
          renderValue={(selected) => displayName(selected)}
          displayEmpty
          variant="outlined"
        >
          <MenuItem disabled value="">
            <em>Select</em>
          </MenuItem>
          {
            props.options.map((option: Options, index: number) => <MenuItem key={index} value={option.value}>{option.displayName}</MenuItem>)
          }
        </Select>
      </Box>
    </>
  )
};

export default Dropdown;
