import { Component } from "react";
import ReportList from "../../_shared/components/report-list/report-list";
import './shipment-support.scss';
import AppOrderReportView from "../../_shared/components/app-order-report-view/app-order-report-view";
import AppShipmentOverView from "../../_shared/components/app-shipment-overview/app-shipment-overview";
import AppLoaderService from "../../_service/app-loader-service";
import { requestType } from "../../enum/ReportEnum";
import { logger } from "../../_core/logger.service";
import { IListModel, ReportedList } from "../../_models/app-common-model";
import ShipmentService from "../../_service/app-shipment.service";
import { connect } from "react-redux";
import { saveOrder, saveProduct } from "../../redux/feature/order-slice";
import { IOrderSlice } from "../../_models/order";
import { IProduct } from "../../_models/app-product-model";

type TShipmentSupportState = {
    isLoading: boolean;
    isShipmentPreview: boolean;
    orderList: IListModel;
    showReport: boolean;
    productDetails: IProduct | null;
}

interface ShipmentPropsFromRedux {
    saveOrder: (res: any | null) => void;
    saveProduct: (res: IProduct[] | null) => void;
}

export class ShipmentSupport extends Component<ShipmentPropsFromRedux, TShipmentSupportState> {
    constructor(props: ShipmentPropsFromRedux) {
        super(props);
        this.state = {
            isLoading: false,
            isShipmentPreview: false,
            orderList: {
                result: [],
                endOfFeed: false,
                page: 0
            },
            showReport: true,
            productDetails: null
        }
        this.showShipmentPreview = this.showShipmentPreview.bind(this);
    }

    componentDidMount() {
        this.fetchRequestList();
    };

    onSearch(value: string) {
        this.props.saveOrder(null);
        this.fetchRequestList(value);
    }

    handleCellClick(reportId: number) {
        AppLoaderService.showLoader(true);
        const shipmentData = this.state.orderList.result.find(
            (el) => el.id == reportId
        )
        if (shipmentData) this.saveOrderToRedux(shipmentData);
        AppLoaderService.showLoader(false);
    }

    showShipmentPreview(flag: boolean, res?: IProduct) {
        if (res) this.setState({ isShipmentPreview: flag, productDetails: res });
        this.setState({ isShipmentPreview: flag });
    }

    fetchRequestList(searchTerm?: string, status = 0) {
        AppLoaderService.showLoader(true);
        const type = requestType.order;
        ShipmentService.getOrderReportList(status, type, searchTerm)
            .then((res: any) => {
                if (res.result.length) this.saveOrderToRedux(res.result[0]);
                this.setState(prevState => ({
                    ...prevState,
                    orderList: res,
                    showOrder: res && res.result?.length
                }), () => {
                    AppLoaderService.showLoader(false);
                });
            })
            .catch((err: any) => {
                logger.debug(err);
                AppLoaderService.showLoader(false);
            })
    }

    saveOrderToRedux(ordersReportList: ReportedList) {
        if (this.props.saveOrder) this.props.saveOrder(ordersReportList);
        if (ordersReportList.order) this.getOrderDetails(ordersReportList.order);
    }

    getOrderDetails(orderId: number) {
        ShipmentService.getOrderDetailsById(orderId).then((res) => {
            this.props.saveProduct(res);
        }).catch((error) => {
            logger.error(error);
        })
    }

    loadMore(event: { page: number, event: string }) {
        console.log(event.page, event.event);
        
    }

    render() {
        return (
            <>
                {
                    this.state.isShipmentPreview ?
                        <AppShipmentOverView navigateToBack={(e) => this.showShipmentPreview(e)} fulfilment={1207} producDetails={this.state.productDetails} />
                        :
                        <div className='order_container'>
                            <ReportList
                                handleSearch={(e) => this.onSearch(e)}
                                userList={this.state.orderList}
                                onCellClick={(e) => this.handleCellClick(e)}
                                isLoading={this.state.isLoading}
                                loadMore={(e) => this.loadMore(e)}
                            />
                            <AppOrderReportView trackShipmentClick={(isShow, res) => this.showShipmentPreview(isShow, res)} />
                        </div>
                }
            </>
        );
    }
}

const orderState = (state: IOrderSlice) => ({
    reportDetail: state.data
});

const orderDispatch = {
    saveOrder,
    saveProduct
};

export default connect(orderState, orderDispatch)(ShipmentSupport);
