import './report-list.scss';
import SearchBar from '../search-bar/search-bar';
import { IListModel, ReportedList } from '../../../_models/app-common-model';
import { Avatar, Box, CircularProgress, Skeleton } from '@mui/material';
import { useEffect, useRef, useState } from 'react';
import { truncateMiddle } from '../../../_core/helpers/helper';
import AppBubbleLoader from '../app-bubble-loader/app-bubble-loader';

interface ReportListProps {
    onCellClick: (index: number) => void;
    handleSearch: (value: string) => void;
    userList: IListModel;
    isLoading?: boolean;
    searchText?: string;
    loadMore: (event: {
        page: number
        event: string
    }) => void;
}

const ReportList: React.FC<ReportListProps> = ({ onCellClick, handleSearch, userList, isLoading, searchText, loadMore }) => {
    const [highlightItem, setHighlightItem] = useState<number | undefined>(0);
    const divRef = useRef<HTMLDivElement>(null);
    const [isBottom, setIsBottom] = useState<boolean>(false);
    const [bubbleLoader, setBubbleLoader] = useState<boolean>(false);

    useEffect(() => {
        setHighlightItem(userList.result.at(0)?.id ?? undefined)
    }, [userList])

    useEffect(() => {
        setBubbleLoader(false)
    }, [userList.result])

    const onChangeHandler = (ev: string) => {
        handleSearch(ev);
        setIsBottom(false);
    }

    const listViewClick = (ev: React.MouseEvent, reportId: number) => {
        onCellClick(reportId);
        setHighlightItem(reportId);
    }

    const scrollHandler = (ev: React.UIEvent<HTMLDivElement, UIEvent>) => {
        if (divRef.current && !userList.endOfFeed && !isBottom && (divRef.current.scrollTop + divRef.current.clientHeight >= divRef.current.scrollHeight)) {
            console.log('Reached the bottom of the div!');
            setIsBottom(true);
            loadMore({page: userList.page += 1, event: "scroll"});
            setBubbleLoader(true);
        }

    }

    return (
        <>
            {isLoading ?
                <Box>
                    <Skeleton animation='wave' style={{ height: 10 }} />
                    <Skeleton animation='wave' />
                    <Skeleton animation='wave' />
                    <Skeleton animation={false} />
                </Box>
                :
                <div className='report-list'>
                    <div className='search_container'>
                        <SearchBar
                            changeHandler={onChangeHandler}
                            searchText={searchText}
                        />
                    </div>
                    <div ref={divRef} className='list_body' onScroll={(ev) => scrollHandler(ev)}>
                        {userList.result.map((report: any, index) => (
                            <div className={`list-cell ${highlightItem === report.id ? 'highlight' : ''}`} key={index}>
                                <div className='list-items' onClick={(e) => listViewClick(e, report.id)} >
                                    <div className='list-content'>
                                        <Avatar className='report-list-profile' src={report.profile_pic}></Avatar>
                                        <div className='query-container'>
                                            <label className='regular-18 font-color'>{truncateMiddle((report.display_name ?? report.name) + ' - #' + report.id)}</label>
                                            <label className='regular-14 lite-color'>{report.question}</label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                    {
                        bubbleLoader ? <Box sx={{ display: 'flex', justifyContent: "center", paddingBottom: "15px" }}>
                            <CircularProgress />
                        </Box> : ""
                    }
                </div>
            }
        </>
    );
}

export default ReportList;
