import { Grid } from '@mui/material';
import './app-shipping-details.scss';
import { IPackageDetails, IProduct, IProductDetails } from '../../../_models/app-product-model';
import { useEffect, useState } from 'react';
import { Package_Keys_To_Extract, Product_Keys_To_Extract } from '../../../_core/constant';
import placeHolderIcon from '../../../../assets/icons/image_placeholder.svg';
import { getAddress, getDate } from '../../../_core/helpers/helper';
import ImageCard from '../app-image-card/image-card';
import productMock from '../../../../mock/order-sample.json';
import { PackageKeys, ProductKeys } from '../../../enum/order.enum';

type TAppShippingDetailsProps = {
  product: IProduct | null;
}

const AppShippingDetails = (props: TAppShippingDetailsProps) => {
  const [orderDetails, setOrderDetails] = useState<IProduct | null>(null);

  useEffect(() => {
    if (props.product) formApiResponse(props.product);
  }, [props]);

  function formApiResponse(product: IProduct) {
    const newResponse = {
      ...product,
      productDetails: filterObject(product),
      customPackageDetails: filterPackage(product.packageDetails)
    }
    setOrderDetails(newResponse);
  }

  function filterObject(product: IProduct): IProductDetails[] {
    const newArray = Product_Keys_To_Extract.map(key => {
      const newObj: IProductDetails = {
        name: key,
        value: product[key]
      }
      return newObj;
    });
    return newArray;
  }

  function filterPackage(packageDetails: IPackageDetails | null): IProductDetails[] | null {
    if (packageDetails) {
      const newArray = Package_Keys_To_Extract.map(key => {
        const newObj: IProductDetails = {
          name: key,
          value: key != 'pickUp' ? packageDetails[key] : getDate(packageDetails[key] ?? '')
        }
        return newObj;
      });
      return newArray;
    }
    return null;
  }

  return (
    <div className='shipment_details_container'>
      <div className='shipment_grid_c_container'>
        <Grid container columnSpacing={3}>
          <Grid item md={12} lg={5} xl={5}>
            {orderDetails?.shipFrom &&
              <div className='ship_from_content'>
                <label className='ship_from_user_name'>Ship From</label>
                <img src={orderDetails?.shipFrom.image ? orderDetails?.shipFrom.image : placeHolderIcon} className='shipment_profile_content' />
                <label className='ship_user_name_c'>{orderDetails.shipFrom.name}</label>
                <p className='address_text_c'>{getAddress(orderDetails.shipFrom.addressLine1, orderDetails.shipFrom.addressLine2)}</p>
              </div>
            }
          </Grid>
          <Grid item md={0} lg={2} xl={2}>
            <div className='seperator_container'>
              <div className='shipment_seperator'></div>
            </div>
          </Grid>
          <Grid item md={12} lg={5} xl={5}>
            {orderDetails?.shipTo &&
              <div className='ship_to_content'>
                <label className='ship_to_user_name'>Ship To</label>
                <img src={orderDetails?.shipTo.image ? orderDetails?.shipTo.image : placeHolderIcon} className='shipment_profile_content' />
                <label className='ship_user_name_c'>{orderDetails.shipTo.name}</label>
                <p className='address_text_c'>{getAddress(orderDetails.shipTo.addressLine1, orderDetails.shipTo.addressLine2)}</p>
              </div>
            }
          </Grid>
        </Grid>
        <div>
          {orderDetails?.productDetails &&
            <div>
              <div className="product_detail_header_container">
                <label className="product_detail_secondary_header">Order Details</label>
              </div>
              <div className='prodct_detail_body'>
                <div className="product_grid_container">
                  {orderDetails.productDetails
                    &&
                    orderDetails.productDetails.map((item: IProductDetails, index: number) => {
                      return <div key={index} className="product_grid_item">
                        <label className='product_details_name'>{ProductKeys[item.name as keyof typeof ProductKeys] || item.name}:</label><br />
                        <div className='p-t-5'>
                          <label className='prduct_detail_value'>{item.value}</label>
                        </div>
                      </div>
                    })
                  }
                </div>
              </div>
            </div>
          }
        </div>
        <div>
          {orderDetails?.customPackageDetails &&
            <div>
              <div className="product_detail_header_container">
                <label className="product_detail_secondary_header">Package Details</label>
              </div>
              <div className='prodct_detail_body'>
                <div className="product_grid_container">
                  {orderDetails.customPackageDetails
                    &&
                    orderDetails.customPackageDetails.map((item: IProductDetails, index: number) => {
                      return <div key={index} className="product_grid_item">
                        <label className='product_details_name'>{PackageKeys[item.name as keyof typeof PackageKeys] || item.name}:</label><br />
                        <div className='p-t-5'>
                          <label className='prduct_detail_value'>{item.value ?? '--'}</label>
                        </div>
                      </div>
                    })
                  }
                </div>
              </div>
            </div>
          }
        </div>
        {(props.product?.sellerAckData || props.product?.buyerAckData) &&
          <div className='shipment_note'>
            <div className="product_detail_header_container">
              <label className="product_detail_secondary_header">Documents</label>
            </div>
            <div className='prodct_detail_body'>
              <Grid container columnSpacing={2}>
                <Grid item md={12} lg={6} xl={6}>
                  <label className='product_details_name'>Seller's Note:</label>
                  <p className='note_description'>{props.product.sellerAckData?.notes}</p>
                  {props.product.sellerAckData?.attachment &&
                    <ImageCard images={props.product.sellerAckData?.attachment} />
                  }
                </Grid>
                <Grid item md={12} lg={6} xl={6}>
                  <label className='product_details_name'>Buyer's Note:</label>
                  <p className='note_description'>{props.product.buyerAckData?.notes}</p>
                  {props.product.buyerAckData?.attachment &&
                    <ImageCard images={props.product.buyerAckData?.attachment} />
                  }
                </Grid>
              </Grid>
            </div>
          </div>
        }
        {/* {(productMock[0].sellerAckData || productMock[0].buyerAckData) &&
          <div className='shipment_note'>
            <div className="product_detail_header_container">
              <label className="product_detail_secondary_header">Documents</label>
            </div>
            <div className='prodct_detail_body'>
              <Grid container columnSpacing={2}>
                <Grid item md={12} lg={6} xl={6}>
                  <label className='product_details_name'>Seller's Note:</label>
                  <p className='note_description'>{productMock[0].sellerAckData?.notes}</p>
                  {productMock[0].sellerAckData?.attachment &&
                    <ImageCard images={productMock[0].sellerAckData?.attachment} />
                  }
                </Grid>
                <Grid item md={12} lg={6} xl={6}>
                  <label className='product_details_name'>Buyer's Note:</label>
                  <p className='note_description'>{productMock[0].buyerAckData?.notes}</p>
                  {productMock[0].buyerAckData?.attachment &&
                    <ImageCard images={productMock[0].buyerAckData?.attachment} />
                  }
                </Grid>
              </Grid>
            </div>
          </div>
        } */}
      </div>
    </div >
  )
}

export default AppShippingDetails;
